import React from 'react';
import { saveSvgAsPng } from 'save-svg-as-png';
import certificateImage from './images/Certificate-01-01.png';
import Axios from 'axios';
function Icon(props) {
  const [uid, setUid] = React.useState('');

  function createGist() {
    const UID = `CERT-UID-${[...Array(48)]
      .map((i) => (~~(Math.random() * 36)).toString(36))
      .join('')}`;

    Axios({
      method: 'POST',
      url: 'https://senteccertificate-4935.restdb.io/rest/userdata',
      headers: {
        'cache-control': 'no-cache',
        'x-apikey': props.apikey,
        'content-type': 'application/json',
      },
      data: {
        name: props.name,
        id: UID,
      },
    })
      .then((response) => {
        setUid(UID);
        saveSvgAsPng(
          document.getElementById('svgCert'),
          `${props.name}_Certificate.jpeg`,
          {
            encoderOptions: 1,
          }
        ).then(() => {
          setUid('');
        });
      })
      .catch((error) => {
        console.log(error);
      });
  }

  return (
    <div style={{ backgroundColor: '#fff' }}>
      <svg
        id='svgCert'
        xmlns='http://www.w3.org/2000/svg'
        xmlnsXlink='http://www.w3.org/1999/xlink'
        data-name='Layer 2'
        viewBox='0 0 1063 768'
      >
        <image
          width='2216'
          height='1600'
          transform='scale(.48)'
          xlinkHref={certificateImage}
        ></image>
        <text fontSize='12' opacity='0.40' x='32%' y='14%' textAnchor='middle'>
          {uid}
        </text>
        <text
          fontFamily='DancingScriptOT'
          fontSize='61'
          opacity='0.47'
          x='50%'
          y='59%'
          textAnchor='middle'
        >
          {props.name}
        </text>
      </svg>
      <button disabled={props.name === ''} onClick={createGist}>
        Click To Generate
      </button>
    </div>
  );
}

export default Icon;
